@import "../bootstrap/less/type.less";

h1 {

	font-size: @font-size-large;
	color: @gray-dark;

	&.n-chart-header {
		font-size: @font-size-max;
        color: @brand-info;
	}
	&.n-product-name {
		font-size: @font-size-large;
        font-weight: bold;
        color: @gray-dark;
	}
}

h2 {
	font-size: @font-size-medium;
	color: @gray-global;
}

.n-calendar-days {
    color: @gray-global;
	font-weight: bold;
}