@import "../bootstrap/less/breadcrumbs.less";


.n-a-breadcrumb-link, .n-a-breadcrumb-link:visited {
  font-size: 11px;
  color: @gray-nav;
  font-weight: lighter;
}

.n-a-breadcrumb-active:visited {
  font-weight: bold;
  color: @gray-nav;
  border-bottom: none;
}

.n-a-breadcrumb-link:hover {
  font-weight: lighter;
  color: @black-hover;
  border-bottom: 1px solid @blue-hover;
  text-decoration: none;
}

.n-a-breadcrumb-active, .n-a-breadcrumb-link:active, .n-a-breadcrumb-active:active,  .n-a-breadcrumb-active:hover{
  font-weight: bold;
  font-size: 11px;
  color: @gray-nav;
  border-bottom: none;
}

.n-a-breadcrumb-icon {
  font-family: 'Icons';
  font-weight: normal;
  font-style: normal;
  text-decoration: none;
  border-bottom: none;
  width: 1em;
  aria-hidden: "true";
  speak: none;
}
.n-a-breadcrumb-icon:after {
  content: "\e61f";
  margin-left: 1px;
  margin-right: 1px;
  color: @gray-medium;
  vertical-align: middle;
}