
.n-scroll {
  overflow: hidden;
  position: relative;
  background: #FFFFFF;
  width:420px;
  border-radius: 4px;
  padding: 3px 2px;
  float: left;
  &[disabled] {
    background-color: #cbcbcb;
  }



  .n-scroll-container {
    overflow: hidden;
    position: relative;
    float: left;
    border-radius: 4px;
    background-color: transparent;
    height: 230px;

    .n-scroll-content {
      position: relative;
      left: 0;
      top: 0;
      margin: 0;
      padding: 5px 5px;
      overflow: hidden;
      float: left;
      &[disabled] {
        color: #aaaaaa;
      }
    }

    .disable-selection {
      -moz-user-select: none; /* Firefox */
      -ms-user-select: none; /* Internet Explorer */
      -khtml-user-select: none; /* KHTML browsers (e.g. Konqueror) */
      -webkit-user-select: none; /* Chrome, Safari, and Opera */
    }

  }

  .n-scroll-container {
    width: 96%;
  }

  .n-scroll-container-x {
    width: 100% !important;
  }

  .n-scroll-bar-x
  {
    bottom: 3px;
    right: 2px !important;
  }

  .n-scroll-bar {
    position: absolute;
    //top: 3px;
    //left: 0;
    bottom: 3px;
    right: 2px;
    height: auto;
    width: auto;
    float: right;
    margin-left: 3px;

    .n-scroll-rail-x {
      height: 8px !important;
      &[disabled] {
        background-color: #f3f3f3;
      }
    }

    .n-scroll-rail {
      -webkit-border-radius: 4px;
      -moz-border-radius: 4px;
      border-radius: 4px;
      width: 8px;
      height: 100%;
      overflow: hidden;
      background-color: rgba(213, 213, 213, .7);
      position: relative;
      padding: 0;
      &[disabled] {
        background-color: #f3f3f3;
      }

      .n-scroll-thumb {
        position: relative;

        margin: 0;
        -webkit-border-radius: 4px;
        -moz-border-radius: 4px;
        border-radius: 4px;
        cursor: pointer;
        color: #333;
        background-color: #666666;
        text-align: center;
        overflow: hidden;

        &:hover {
          //background-color: #063a5d;
          background-color: #0B6DAF;
          color: #ffffff;
        }
      ;
        &:active {
          background-color: #1d1d1d
        }
      ;

        &[disabled] {
          background-color: #a5a5a5;
          cursor: default;
        }
      }

      .n-scroll-thumb {
        width: 8px;
      }


      .n-scroll-thumb-x {
        height: 8px !important;
      }
    }
  }


}

.n-scroll .disable {
  display: none;
}

.n-scroll .noSelect {
  user-select: none;
  -o-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
}


