@import "variables.less";

.fuelux .tree {

    .tree-item {
        margin: 0;

        .tree-item-name {
            display: inline-block;
            width: 100%;
            text-align: left;
            font-weight: normal;
            padding-left: 5px;
            border: 1px solid transparent;
            border-radius: 0;
            color: @n-tree-text-normal-color;
            &:hover {
                color: @n-tree-text-hover-color;
                .tree-label {
                    border-bottom: 1px solid @n-tree-hover-underline-color;
                }
            }
            &:focus {
                border: 1px solid @n-tree-border-focus-color;
            }

            .fueluxicon-bullet {
                font-size: 10px;
                top: 0;
            }

            .glyphicon-ok {
                font-family: fuelux;
                font-size: 10px;
                top: 0;
                &:before {
                    content: '\e009';
                }
            }
        }

        &.tree-selected .tree-item-name {
            background-color: @n-tree-selected-background-color;
            color: @n-tree-text-selected-color;
            &:hover .tree-label {
                border-bottom: 1px solid transparent;
            }
        }
    }

    .icon-folder {
        display: none;
    }

    height: 100%;
    padding: 2px;
    border-radius: 0;
    .tree-branch {
        margin-top: 0;
        margin-bottom: 0;
        .tree-branch-header {
            .glyphicon-play {
                padding-left: 5px;
                padding-right: 0;
                font-size: 14px;
                &:before {
                    content: '\e612';
                }
            }

            .tree-branch-name {
                padding-left: 0;
                width: 100%;
                height: 25px;
                text-align: left;
                border-radius: 0;
                border: 1px solid transparent;
                color: @n-tree-text-normal-color;
                &:hover {
                    color: @n-tree-text-hover-color;
                    .tree-label {
                        border-bottom: 1px solid @n-tree-hover-underline-color;
                    }
                }
                &:focus {
                    border: 1px solid @n-tree-border-focus-color;
                }
            }
        }

        .tree-branch-children {
            margin-left: 0px;
            padding-left: 14px;
        }

        &.tree-selected > .tree-branch-header .tree-branch-name {
            background-color: @n-tree-selected-background-color;
            color: @n-tree-text-selected-color;
            &:hover {
                color: @n-tree-text-selected-color;
            }
        }
    }

    .glyphicon {
        font-family: "Icons";
        top: 3px;
    }

    .tree-open > .tree-branch-header .glyphicon-play {
        top: 0;
    }
}