.n-banner-links-collapse {

  height: 40px;

  &-dropdown-toggle {
    padding: 10px;
    position: absolute;
    right: 0;
    #gradient.vertical(#D8D9DA, #ADAEAE) !important;
    
    &[aria-expanded=true]{
      border-radius: 0;
    }

    &:focus:not(.disabled):not([aria-expanded=true]),
    &:hover:not(.disabled)[aria-expanded=false]{
      padding: 8px;
      border: 2px solid #00a1cc;
      background: #fff !important;
    }
    
  }

  &-dropdown-menu{
    right: 0;
    left: auto;
    li a:hover span{
      border: none;
    }
  }

}