        }

        &:disabled + label {
            color: @input-color-disabled;
            cursor: default;
        }
        &[disabled] + label {
            color: @input-color-disabled;
            cursor: default;
        }

        &:disabled + label:before {
            border: 1px solid @input-border-disabled;
            background: @input-bg-disabled;
            box-shadow: 0px 1px 3px 0px @input-border-disabled;
            color: @input-color-disabled;
