            box-shadow: 0px 1px 3px 0px @input-border, 0px 0px 0px 1px lighten(@input-border-hover, 30%) inset;
        }

        &:active + label:before {
            border: 1px solid @input-border-focus;
            box-shadow: 0px 1px 3px 0px @input-border-focus inset;
        }

        &:focus + label:before {
            border: 1px solid @input-border-focus;
            box-shadow: 0px 1px 3px 0px @input-border-focus;
        }

        // To remove focus border
        &:focus {
            outline: initial;
        }

        &:disabled + label {
            color: @input-color-disabled;
            cursor: default;

        }
        &[disabled] + label {
            color: @input-color-disabled;
            cursor: default;
        }

        &:disabled + label:before {
            border: 1px solid @input-border-disabled;
            background: @input-bg-disabled;
            box-shadow: 0px 1px 3px 0px @input-border-disabled;
            color: @input-color-disabled;
            cursor: default;
        }
    }
}

// OT3 p.77
// Login screen
.n-login-body {
    padding: 0;
    overflow-x: auto;
    overflow-y: auto;
    min-height: 390px;
    min-width: 410px;
    height: 100%;
    #gradient > .radial(@gradient-background-radial-center, @gradient-background-radial-far-side);
    background-repeat: repeat;
}

.floater {
    float: left;
    height: 50%;
    margin-bottom: (-@login-form-logo-height - (@login-form-panel-height / 2) - 2px);
}

.n-login-form {
    padding: 0;
    clear: both;
    height: @login-form-height;
    position: relative;
    margin: 0 auto;
    width: 100%;
    .n-login-logo {
        width: 400px;
        margin: 0 auto;
        text-align: right;
        padding-right: 5px; // 5 from dialog edge
        margin-bottom: 43px; // 43 distance to version and system info
    }
    .panel {
        width: 400px;
        margin: 0 auto;
        .panel-heading {
            padding: 24px 0px 0 26px;
        }
        button {
            margin: 0;
        }

        .panel-body {
            h2.n-login-title-version-light:extend(.title-version-light) {
                // see also utilities.less
                padding-top: 7px; // 25 - compromise between 24 and 26 to simplify gaps
                padding-left: 4px;
                margin-top: 0;
            }

            .alert-error{
                padding-left: 5px;
                padding-bottom: 20px;
                > .icon:first-child {
                    top: 3px;
                    position: relative;
                    font-size: @font-size-medium;
                    margin-right: 5px;
                }
            }

            .n-login-panel-fields {
                padding-top: 25px;
                padding-right: 0;
                .input-required {
                    padding: 0px 5px 0px 5px;
                }
            }
        }

        form.form-horizontal {
            margin-right: 28px;
        }
    }
}

.n-login-textfields {
    display: inline-block;
    padding-left: 14px;
    padding-right: 10px;
    .form-control-feedback {
        padding-top: 1px;
        right: 4px !important;
    }
}

.n-login-button {
    margin-top: 0; // 20 from field
    float: right;
}

.floater-bottom {
    width: 100%;
    float: left;
    height: 50%;
    margin-top: (-(@login-form-panel-height / 2) + 2px);
    margin-bottom: -18px;
}

.n-login-copyright {
    clear: both;
    position: relative;
    width: 100%;
    text-align: center;
    height: 18px;
    color: @login-text-color;
}
