@import "variables.less";

// Link
// -------------------------

a, a.link {
    color: @blue-selection;
    font-weight: bold;
}

a.visited {
    color: @gray-dark;
    border-bottom: 1px solid @gray-medium;
}

// TEMP solution to prevent the keyboard focus from changing the color (until proper focus prevention found)
a.disabled, a.disabled:focus, a[disabled], a[disabled]:focus {
    color: @gray-disabled;
    font-weight: bold;
    border-bottom: 1px solid @gray-disabled;
    pointer-events: none;
    cursor: default;
}

a:hover, a.link:hover, a.visited:hover, a:focus, a.link:focus, a.visited:focus {
    text-decoration: none;
}

//ToDo move this to some generic file for better visibility
[data-icon]:before {
    font-family: 'Glyphicons Halflings';
    content: attr(data-icon);
    speak: none;
}

//ToDo disabled link need to be excluded from tab order)
.n-a:extend(a all) {
    &-link {
        color: @blue-selection;
        font-weight: bold;
    }

    &-visited {
        color: @gray-dark;
        border-bottom: 1px solid @gray-medium;
    }

    &:hover, &-link:hover, &-visited:hover {
        color: @blue-selection;
        border-bottom: 1px solid @blue-hover;
        text-decoration: none;
    }
    &-disabled, &-disabled:focus, &[disabled], &[disabled]:focus {
        color: @gray-disabled;
        font-weight: bold;
        border-bottom: 1px solid @gray-disabled;
        pointer-events: none;
        cursor: default;
    }

    &-secondary {
        &-link {
            font-size: 12px;
            color: @blue-selection;
            font-weight: bold;
        }

        &-visited {
            font-size: 12px;
            font-weight: bold;
            color: @gray-dark;
            border-bottom: 1px solid @gray-medium;
        }

        //TODO link disabling - temp solution to prevent the keyboard focus from changing the color (until proper focus prevention found)
        &-disabled, &-disabled:focus, &[disabled], &[disabled]:focus {
            font-size: 12px;
            color: @gray-disabled;
            font-weight: bold;
            border-bottom: 1px solid @gray-disabled;
            pointer-events: none;
            cursor: default;
        }

        &:hover, &-link:hover, &-visited:hover {
            color: @blue-selection;
            border-bottom: 1px solid @blue-hover;
            text-decoration: none;
        }
    }

    &-call-to-action {
        &-link {
            font-size: 14px;
            color: @blue-selection;
            font-weight: bold;
            position: relative;
        }

        &-visited {
            font-size: 14px;
            font-weight: bold;
            color: @gray-dark;
            border-bottom: 1px solid @gray-medium;
        }

        //TODO link disabling - temp solution to prevent the keyboard focus from changing the color (until proper focus prevention found)
        &-disabled, &-disabled:focus, &[disabled], &[disabled]:focus {
            font-size: 14px;
            color: @gray-disabled;
            font-weight: bold;
            border-bottom: 1px solid @gray-disabled;
            pointer-events: none;
            cursor: default;
        }

        &:hover, &-link:hover, &-visited:hover {
            color: @blue-selection;
            border-bottom: 1px solid @blue-hover;
            text-decoration: none;
        }

        //TODO link disabling - temp this might not be required
        &-link:after, &-visited:after, &-disabled:after, &[disabled]:after {
            //font-family: 'Glyphicons Halflings';
            //content: "\e080";
            font-weight: normal;
            font-style: normal;
            //position: absolute;
            //right: 100%;
            //top: 0;
            //bottom: 0;
            //display: inline-block;
            //padding-left: 1em;
            text-decoration: none;
            border-bottom: none;
            //width: 1em;
            aria-hidden: "true";
            speak: none;
        }

        //TODO link disabling - temp this might not be required
        &-icon, &-icon:hover {
            //font-family: 'Glyphicons Halflings';
            //content: "\e080";
            //font-weight: normal;
            //font-style: normal;
            //position: absolute;
            //right: 100%;
            //top: 0;
            //bottom: 0;
            display: inline-block;
            //padding-left: 1em;
            text-decoration: none;
            border-bottom: none;
            //width: 1em;
            aria-hidden: "true";
            speak: none;
        }
    }
}

//TODO disclosure icon should behave as a link as well (without underlining)

.n-call-to-action-icon {
    font-family: 'Icons';
    font-weight: normal;
    font-style: normal;
    text-decoration: none;
    border-bottom: none;
    width: 1em;
    aria-hidden: "true";
    speak: none;
}
span.n-call-to-action-icon:after {
    content: "\e61f";
    vertical-align: middle;
    font-size:small;
}
a.n-link-disabled + span.n-call-to-action-icon:after {
    color:#999999;
}
